<template>
	<form
		action
		class="form"
		@submit.prevent="submit"
	>
		<h4 class="form__title">Payment</h4>
		<div class="form__notification">
			<img
				src="@/assets/img/lock.png"
				alt=""
			>
			This is a secure 128-bit SSL encrypted payment
		</div>
		<div class="form__section">
			<div class="form__field">
				<label class="form__label">
					<span>Cardholder Name</span>
					<input
						data-required="true"
						type="text"
						placeholder="Name as it appears on your card"
						:value="$store.state.pay.name"
						@input="$store.commit('pay/SET_CARD_NAME', $event.target.value)"
						class="form__input"
						data-error-message="Please enter recipient full name"
					/>
				</label>
			</div>
		</div>
		<div class="form__section">
			<div class="form__field form__field_card-number">
				<label class="form__label">
					<span>Card Number</span>
					<img
						v-if="this.cardType === '' || this.cardType === 'unknown' ? false : true"
						class="form__field-icon"
						:src='require("../assets/img/" + this.cardType + ".png")'
						alt
					/>
					<cleave
						data-required="true"
						placeholder="XXXX XXXX XXXX XXXX XXXX"
						:value="$store.state.pay.number"
						@input="numberEnter"
						:options="cardNumOptions"
						class="form__input"
					></cleave>
				</label>
			</div>
		</div>

		<div
			class="form__section"
			style="justify-content: flex-start"
		>
			<div
				class="form__field"
				style="max-width: 105px; margin-right: 30px"
			>
				<label class="form__label">
					<span>Expire Date</span>
					<cleave
						data-required="true"
						placeholder="MM / YY"
						:value="$store.state.pay.date"
						@input="dateEnter"
						:options="cardDateOptions"
						class="form__input"
					></cleave>
				</label>
			</div>

			<div
				class="form__field"
				style="max-width: 105px; margin-bottom: 10px"
			>
				<label class="form__label">
					<span>Security Code</span>
					<cleave
						data-required="true"
						placeholder="0000"
						:value="$store.state.pay.code"
						@input="codeEnter"
						:options="cardCodeOptions"
						class="form__input"
					></cleave>
				</label>
			</div>

		</div>
		<button class="form__submit">Pay Securely</button>
	</form>
</template>

<script>
import Cleave from 'vue-cleave-component';
import { log } from 'util';

export default {
	data() {
		return {
			errArr: [],
			cardType: '',
			cardNumOptions: {
				creditCard: true,
				onCreditCardTypeChanged: type => {
					this.cardType = type;
				}
			},
			cardDateOptions: {
				date: true,
				datePattern: ['m', 'y']
			},
			cardCodeOptions: {
				blocks: [1, 1, 1, 1],
				numericOnly: true
			}
		};
	},
	methods: {
		submit() {
			let phonePattern = /^(\+7|7|8)?(\s|\-)?\(?([0-9]{3})\)?(\s|\-)?([0-9]{3})(\s|\-)?([0-9]{2})(\s|\-)?([0-9]{2}$)/;
			let emailPatter = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
			this.errArr = [];

			Array.prototype.forEach.call(this.$el.elements, (input, index) => {
				if (input.tagName === "INPUT" || input.tagName === "SELECT") {
					if (input.dataset.required === "true") {
						let err = document.createElement("span");
						let timeOut;

						err.classList = "form__error active";
						if (input.value === "") {
							err.innerText = input.dataset.errorMessage || "Empty field or other error";
						}

						if (input.type == "tel" && !phonePattern.test(input.value)) {
							err.innerText = "Please edit number format (+7 999 999-99-99)";
						}

						if (input.dataset.type == "email" && !emailPatter.test(input.value)) {
							err.innerText = "Please edit email address";
						}

						if (err.innerText) {
							this.errArr.push({ id: index, value: err.innerText });
							if (!input.closest('.form__field').querySelector(".form__error.active")) {
								input.closest('.form__field').append(err);
								timeOut = setTimeout(() => {
									input.closest('.form__field').querySelector(".form__error.active").remove();
								}, 3000);
							}

							input.addEventListener("focus", () => {
								clearTimeout(timeOut);
								input.closest('.form__field').querySelector(".form__error.active") ? input.closest('.form__field').querySelector(".form__error.active").remove() : "";
							});
						}
					}
				}
			});

			if (!this.errArr.length) {
				console.log("Pay form status: Success");
				this.$store.commit('SET_STEP', ++this.$store.state.steps.current)
			}
		},
		numberEnter(value) {
			this.$store.commit('pay/SET_CARD_NUMBER', value)
		},
		dateEnter(value) {
			this.$store.commit('pay/SET_CARD_DATE', value)
		},
		codeEnter(value) {
			this.$store.commit('pay/SET_CARD_CODE', value)
		}
	}
};
</script>

<style></style>
