						<Pay v-else-if="$store.state.steps.current === 3"></Pay>
						<Success v-else-if="$store.state.steps.current === 4"></Success>
					</div>
				</div>
				<div class="main__right">
					<Busket></Busket>
					<div class="main__copyright">
						All purchases are subject to our
						<a href="#">Terms and Conditions</a>.
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Breadcrumbs from "@/components/Breadcrumbs";
import Busket from "@/components/Busket";
import Shipping from "@/components/Shipping";
import Billing from "@/components/Billing";
import Pay from "@/components/Pay";
import Success from "@/components/Success";

export default {
	name: "Main",
	components: { Breadcrumbs, Busket, Shipping, Billing, Pay, Success },
