<template>
	<form
		action
		class="form"
		@submit.prevent="submit"
	>
		<h4 class="form__title">Billing Information</h4>
		<div class="form__section">
			<span class="form__subtitle">Billing Contact</span>
			<div class="form__field">
				<input
					data-required="true"
					type="text"
					placeholder="Full Name"
					:value="$store.state.billing.name"
					@input="$store.commit('billing/SET_NAME', $event.target.value)"
					class="form__input"
					data-error-message="Please enter recipient full name"
				/>
			</div>
			<div class="form__field">
				<input
					data-required="true"
					data-type="email"
					type="text"
					placeholder="Email Address"
					class="form__input"
					:value="$store.state.billing.email"
					@input="$store.commit('billing/SET_EMAIL', $event.target.value)"
					data-error-message="Please enter email"
				/>
			</div>
		</div>
		<div class="form__section">
			<span class="form__subtitle">Billing Address</span>
			<div class="form__field">
				<input
					data-required="true"
					type="text"
					placeholder="Street Address"
					:value="$store.state.billing.addressStreet"
					@input="$store.commit('billing/SET_ADDRESSSTREET', $event.target.value)"
					class="form__input"
				/>
			</div>
			<div class="form__field">
				<input
					type="text"
					placeholder="Apt, Suite, Bldg, Gate Code. (optional)"
					:value="$store.state.billing.addressHome"
					@input="$store.commit('billing/SET_ADDRESSHOME', $event.target.value)"
					class="form__input"
				/>
			</div>
			<div class="form__field form__field_target">
				<span
					:class="'form__field-icon' + ($store.getters.checkIsGeolocation ? '' : ' disable')"
					@click="getGeolocation"
				>
					<img
						src="@/assets/img/geo.png"
						alt
					/>
				</span>
				<input
					data-required="true"
					type="text"
					placeholder="City"
					class="form__input"
					:value="$store.state.billing.geoCity"
					@input="$store.commit('billing/SET_CITY', $event.target.value)"
				/>
			</div>
			<div class="form__field form__field_country">
				<!-- <select
					data-required="true"
					name
					:class="'form__select' + (!$store.state.billing.geoCountry ? ' empty' : '')"
					:value="$store.state.billing.geoCountry"
					@change="$store.commit('billing/SET_COUNTRY', $event.target.value)"
				>
					<option value selected disabled hidden>Select country</option>
					<option value="Russia">Russia</option>
					<option value="England">England</option>
					<option value="German">German</option>
				</select> -->
				<v-autocomplete
					:value="$store.state.billing.geoCountry"
					:items="$store.getters.countryListNames"
					data-required="true"
					dense
					filled
					background-color="transparent"
					placeholder="Select country"
					hide-details=true
					append-icon=""
				>
				</v-autocomplete>
			</div>
			<div class="form__field form__field_index">
				<input
					data-required="true"
					type="number"
					placeholder="ZIP"
					class="form__input"
					:value="$store.state.billing.geoZip"
					@input="$store.commit('billing/SET_ZIP', $event.target.value)"
				/>
			</div>
		</div>
		<button class="form__submit">Continue</button>
	</form>
</template>

<script>
export default {
	data() {
		return {
			errArr: [],
		};
	},
	methods: {
		submit() {
			let phonePattern = /^(\+7|7|8)?(\s|\-)?\(?([0-9]{3})\)?(\s|\-)?([0-9]{3})(\s|\-)?([0-9]{2})(\s|\-)?([0-9]{2}$)/;
			let emailPatter = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
			this.errArr = [];

			Array.prototype.forEach.call(this.$el.elements, (input, index) => {
				if (input.tagName === "INPUT" || input.tagName === "SELECT") {
					if (input.dataset.required === "true") {
						let err = document.createElement("span");
						let timeOut;

						err.classList = "form__error active";
						if (input.value === "") {
							err.innerText = input.dataset.errorMessage || "Empty field or other error";
						}

						if ( input.type == "tel" && !phonePattern.test(input.value)) {
							err.innerText = "Please edit number format (+7 999 999-99-99)";
						}

						if ( input.dataset.type == "email" && !emailPatter.test(input.value)) {
							err.innerText = "Please edit email address";
						}

						if (err.innerText) {
							this.errArr.push({id: index, value: err.innerText});
							if (!input.closest('.form__field').querySelector(".form__error.active")) {
								input.closest('.form__field').append(err);
								timeOut = setTimeout(() => {
									input.closest('.form__field').querySelector(".form__error.active").remove();
								}, 3000);
							}

							input.addEventListener("focus", () => {
								clearTimeout(timeOut);
								input.closest('.form__field').querySelector(".form__error.active") ? input.closest('.form__field').querySelector(".form__error.active").remove() : "";
							});
						}
					}
				}
			});

			if (!this.errArr.length) {
				console.log("Billing form status: Success");
				this.$store.commit('SET_STEP', ++this.$store.state.steps.current)
			}
		},
		getGeolocation() {
			this.$store.commit(`billing/SET_COUNTRY`, this.$store.state.geoLocationData.country_name);
			this.$store.commit(`billing/SET_CITY`, this.$store.state.geoLocationData.city);
			this.$store.commit(`billing/SET_ZIP`, this.$store.state.geoLocationData.zip);
		}
	}
};
</script>

<style></style>
