import { FORM } from "./mutation-types";

const state = {
    geoCountry: "",
    geoCity: "",
    geoZip: "",
    name: "",
    email: "",
    addressStreet: "",
    addressHome: ""
}

const mutations = {
    [FORM.SET_COUNTRY](state, payload) {
        state.geoCountry = payload;
    },
    [FORM.SET_CITY](state, payload) {
        state.geoCity = payload;
    },
    [FORM.SET_ADDRESSSTREET](state, payload) {
        state.addressStreet = payload;
    },
    [FORM.SET_ADDRESSHOME](state, payload) {
        state.addressHome = payload;
    },
    [FORM.SET_ZIP](state, payload) {
        state.geoZip = payload;
    },
    [FORM.SET_NAME](state, payload) {
        state.name = payload;
    },
    [FORM.SET_EMAIL](state, payload) {
        state.email = payload;
    },
}

const actions = {

}

export default {
    state,
    mutations,
    actions
}