import Vue from "vue";
import Vuex from "vuex";

import { FORM } from "./mutation-types";

import shipping from './shipping';
import billing from './billing';
import pay from './pay';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		isGeolocation: false,
		geoLocationData: null,
		countryList: [],
		steps: {
			current: 1,
			length: 4
		},
	},
	getters: {
		checkIsGeolocation: state => {
			return state.geoLocationData === null ? state.isGeolocation = false : state.isGeolocation = true
		},
		countryListNames: state => {
			return state.countryList.map(item => item.name)
		}
	},
	mutations: {
		[FORM.SET_LOCATIONDATA](state, payload) {
			state.geoLocationData = payload;
		},
		[FORM.SET_STEP](state, payload) {
			state.steps.current = payload
		},
		[FORM.SET_COUNTRYLIST](state, payload) {
			state.countryList = payload
		}
		
	},
	actions: {
		getGeolocation({ state, commit }) {
			navigator.geolocation.getCurrentPosition(
				async pos => {
					let ip = await this._vm.$axios.get(
						"http://api.ipify.org/?format=json"
					);
					let locationInfo = await this._vm.$axios.get(
						`http://api.ipstack.com/${ip.data.ip}?access_key=118f773e95a258cc236e5bb158b36fca`
					);
					commit(FORM.SET_LOCATIONDATA, locationInfo.data);
				},
				err => {
					console.log(err);
				}
			);
		},
		getCountryList({state, commit}) {
			this._vm.$axios({
				method: "GET",
				url: "https://ajayakv-rest-countries-v1.p.rapidapi.com/rest/v1/all",
				headers: {
					"content-type": "application/octet-stream",
					"x-rapidapi-host": "ajayakv-rest-countries-v1.p.rapidapi.com",
					"x-rapidapi-key":
						"299fe45fc6msh95452e5eab7f2c8p1f2be6jsn8cffccf41a0c"
				}
			})
				.then(response => {
					commit(FORM.SET_COUNTRYLIST, response.data);
				})
				.catch(error => {
				});
		}
	},
	modules: {
		shipping: {
			namespaced: true,
			state: shipping.state,
			mutations: shipping.mutations,
			actions: shipping.actions
		},
		billing: {
			namespaced: true,
			state: billing.state,
			mutations: billing.mutations,
			actions: billing.actions
		},
		pay: {
			namespaced: true,
			state: pay.state,
			mutations: pay.mutations,
			actions: pay.actions
		}
	}
});
