import { FORM } from "./mutation-types";

const state = {
    name: "",
    number: "",
    date: "",
    code: ""
}

const mutations = {
    [FORM.SET_CARD_NAME](state, payload) {
        state.name = payload;
    },
    [FORM.SET_CARD_NUMBER](state, payload) {
        state.number = payload;
    },
    [FORM.SET_CARD_DATE](state, payload) {
        state.date = payload;
    },
    [FORM.SET_CARD_CODE](state, payload) {
        state.code = payload;
    },
}

const actions = {

}

export default {
    state,
    mutations,
    actions
}